<template>
    <div class="dynamic">
      <div class="topSection">
        <div class="backgroundArea" :style="{backgroundImage: 'url(' + backgroundImg + ')'}">
          <div class="header">
            <span @click="back" class="back">
              <img src="../assets/img/back.png" class="releaseIcon">
            </span>
          </div>
          <div class="userHead">
            <img :src="myInfo.userPhoto">
          </div>
        </div>
        <div class="title">动态</div>
      </div>
      <div class="content">
        <div class="row" v-for="(dynamic, index) in dynamics" :key="index">
          <div class="rowHead">
            <span class="publish">
              <div class="headPic">
                <img :src="dynamic.fLogo" class="headImg">
              </div>
              <div class="publishInfor">
                <span class="publisher">{{ dynamic.fNickname }}</span>
                <span class="publishTime">{{ dynamic.fCreatedTime }}</span>
              </div>
            </span>
          </div>
          <div class="blog">{{ dynamic.fMontNote }}</div>
            <div :class="dynamic.medias.length === 4 || dynamic.medias.length === 1 ? picArea2 : picArea1" v-if="dynamic.fCatType === '0'">
              <span :class="dynamic.medias.length === 1 ? img1 : img3" v-for="(media, index) in dynamic.medias" :key="index">
                <img :src="media.media">
              </span>
            </div>
            <div class="videoArea" v-if="dynamic.fCatType === '1'">
              <video class="picArea2" controls :poster="dynamic.fMedia1">
                <source :src="dynamic.fMedia2">
              </video>
            </div>
          </div>
      </div>
    </div>
</template>

<script>
import { getMomentsList, getUserInfo } from '../api/getdata'
import store from '../vuex/store'
export default {
  name: 'dynamic',
  data () {
    return {
      myInfo: {},
      backgroundImg: require('../assets/img/momentBackground.png'),
      img1: 'img1',
      img3: 'img3',
      picArea1: 'picArea1',
      picArea2: 'picArea2',
      dynamics: []
    }
  },
  mounted () {
    this.getUserProfile()
    this.getDynamicList()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getUserProfile () {
      let data = getUserInfo({fUserId: store.state.userId})
      data.then(res => {
        console.log(res)
        this.myInfo = res.data
      })
    },
    getDynamicList () {
      let data = getMomentsList({userId: store.state.userId, pageIndex: 1, pageSize: 10000})
      data.then(res => {
        console.log(res)
        this.dynamics = res.data.data
      })
    }
  }
}
</script>

<style scoped>
  * {
      margin: 0;
      padding: 0;
  }

  .topSection {
    width: 100%;
    height: 6rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 99;
    background: #fff;
  }

  .backgroundArea {
    height: 4.74667rem;
    background-size: 100% 100%;
    background-position: center;
    background-repeat: no-repeat;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .back {
    height: 0.5rem;
    width: 0.266667rem;
  }

  .back img {
    width: 100%;
  }

  .userHead {
    width: 1.86667rem;
    height: 1.86667rem;
    border-radius: 50%;
    margin-left: 0.4rem;
    margin-top: 0.7rem;
    overflow: hidden;
  }

  .title {
    padding: 0 0.4rem;
    text-align: start;
    font-size: 0.42667rem;
    margin-top: 0.4rem;
    margin-left: 0.21333rem;
    position: relative;
  }

  .title::before {
    content: '';
    display: block;
    position: absolute;
    width: 0.05333rem;
    height: 100%;
    left: 0.18667rem;
    top: 0;
    background: #ffd200;
  }

  .content {
    margin-top: 6rem;
    border-bottom: 0.1066667rem solid #f5f5f5;
    z-index: 0;
    width: 100%;
  }

  .row {
    width: calc(100% - 0.625rem);
    margin: 0 auto;
    margin-top: 0.74667rem;
    border-bottom: 0.1rem solid #f5f5f5;
  }

  .picArea2::after,
  .picArea1::after {
    content: "";
    display: block;
    clear: both;
  }

  .rowHead {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .publish {
    display: flex;
    align-items: center;
    width: 100%;
  }
  .publishInfor {
    display: flex;
    align-items: center;
    justify-content: space-between;
    text-align: left;
    margin-left: 0.5rem;
    flex-grow: 1;
  }
  .headPic {
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 50%;
    position: relative;
  }

  .headPic img {
    border-radius: 0.8rem;
  }

  .publisher {
    font-size: 0.42667rem;
    color: #333333;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .publishTime {
    color: #666666;
    font-size: 0.32rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .blog {
    margin-top: 0.5rem;
    text-align: start;
    font-size: 0.4rem;
    color: #333333;
  }

  .picArea1,
  .picArea2 {
    margin-top: 0.4rem;
  }

  .picArea2 {
    width: 66.667%;
  }

  .img3 {
    display: block;
    float: left;
    width: 2.96rem;
    height: 1.947rem;
    margin: 0.12rem 0.08rem;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .headImg {
    width: 100%;
    height: 100%;
  }

  .img1 {
    width: 6rem;
  }

 .usertype {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: -0.1rem;
    bottom: -0.1rem;
    color: #fffcfc;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    border: 2px solid #fffcfc;
    background: #ffd200;
    font-size: 0.373333rem;
  }

  .videoArea {
    text-align: start;
  }
</style>



